// index.js
document.addEventListener("DOMContentLoaded", () => {
    const connectAllButton = document.getElementById("connect-all-btn");

    connectAllButton.addEventListener("click", async () => {
        // Get the active tab
        const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });

        // Execute script on the active tab
        chrome.scripting.executeScript({
            target: { tabId: tab.id },
            func: () => {
                // Select all Connect buttons on the page
                const connectButtons = document.querySelectorAll('button[data-control-name="connect"]');
                
                // Click each Connect button
                connectButtons.forEach(button => {
                    if (button) button.click();
                });
            }
        });
    });
});
